<template>
  <div class="admin-page">
    <div class="container-fluid">
      <div class="row">
        <div class="col-lg-12 col-md-12 col-xs-12 pt-4 pb-4">
          <h2>Mon google photos</h2>
        </div>
        <div @click='addItem(item)' class="item col-lg-4 col-md-6 col-xs-12" v-for="(item, index) in events" :key="index">
          <div class="item_content">
            <div class="item_content--img"><img v-bind:src="item.coverPhotoBaseUrl" /></div>
            <span>{{item.title}}</span>
          </div>
        </div>
      </div>
    </div>
    <modal v-bind:open='open' @close='closeModal' :type='type'></modal>
  </div>
</template>
<script>
import firebase from '../../libs/firebase'
import Modal from './Modals/Confirm.vue'
import Photos from 'googlephotos'

export default {
  props: {
    token: String
  },
  components: {
    Modal
  },
  data() {
    return {
      albumIds: [],
      events: [],
      alreadyExist: '',
      open: false,
      type: ''
    }
  },
  created() {
    this.getEvents((res) => {
      const albumIds = res.map(event => {
        return event.googleId;
      })
      this.albumIds = albumIds
    })

    this.getEventsByGoogle(this.token, (res) => {
      const _this = this;
      const events = res.map(event => {
        if(_this.albumIds.indexOf(event.id) >= 0 ) {
          event.exist = true;
        }
        return event
      })

      this.events = events
    })
  },
  methods: {
    addItem(item) {
      if(item.exist) {
        this.type = 'warning'
        this.open = true
        return
      }

      const photos = new Photos(this.token)
      const _this = this
      new Promise(function(resolve, reject) {
        const res = photos.mediaItems.get(item.coverPhotoMediaItemId)
        if(!res) {
          reject
        }

        resolve(res);
      }).then(function(value) {
        item.baseUrl = value.baseUrl + "=w1280"
        firebase.addItem(item);
        _this.type = 'success'
        _this.open = true
      });
    },
    getEventsByGoogle(token, callback) {
      const photos = new Photos(token)
      const _this = this;

      new Promise(function(resolve, reject) {
        const res = photos.albums.list()
        if(!res) {
          reject
        }

        resolve(res);
      }).then(function(value) {
        callback(value.albums)
      });
    },
    getEvents(callback) {
      firebase.getData((ev) => {
        callback(ev)
      });
    },
    closeModal(){
      this.open = false
    }
  }
};
</script>
<style>
</style>



