import * as firebase from "firebase/app";

// Add the Firebase services that you want to use
import "firebase/auth";
import "firebase/firestore";

const firebaseConfig = {
  apiKey: "AIzaSyA9cuTiFYsnDk1hC0QHMcfzQmymkUU1GcA",
  authDomain: "amandine-and-valou.firebaseapp.com",
  databaseURL: "https://amandine-and-valou.firebaseio.com",
  projectId: "amandine-and-valou",
  storageBucket: "amandine-and-valou.appspot.com",
  messagingSenderId: "383307297320",
  appId: "1:383307297320:web:4fef0326f8a63a35"
};

firebase.initializeApp(firebaseConfig);
const db = firebase.firestore();

export default {
  getData: (callback) => {
    db.collection('events').onSnapshot(function (elem) {
      let events = [];
      if (!elem) {
        callback(null)
        return
      }

      elem.forEach((doc) => {
        const res = doc.data()
        events.push(res)
      })

      callback(events)
    })
  },
  addItem: (item) => {
    const data = {
      title: item.title,
      image: item.baseUrl,
      googleId: item.id
    };

    db.collection("events").add(data)
    .then(function(docRef) {
        console.log("Document written with ID: ", docRef.id);
    })
    .catch(function(error) {
        console.error("Error adding document: ", error);
    });
  }
}



