<template>
  <div>
    <sweet-modal :icon="typeSelected" ref="modal" v-on:close='closeModal'>
      {{ wording }}
    </sweet-modal>
  </div>
</template>

<script>
import { SweetModal } from 'sweet-modal-vue'

export default {
  components: {
    SweetModal
  },
  props: {
    open: Boolean,
    item: Object,
    type: String
  },
  data() {
    return {
      openModal: this.open,
      typeSelected: this.type,
      itemSelected: '',
      wording: ''
    }
  },
  watch: {
    open: function (valeur, ancienneValeur) {
      if(valeur) {
        this.wording = this.type === "success" ? "Ajout avec succs !" : "Dj ajout"
        this.typeSelected = this.type
        this.itemSelected = this.item
        this.$refs.modal.open()
      }
    }
  },
  methods: {
    closeModal() {
      this.$emit('close')
    }
  }
};
</script>
<style>
</style>



