<template>
  <div>
    <sweet-modal class="modal_info" ref="modal" :title="itemSelected.title" width="100%" v-on:close='closeModal'>
      <div>
        <img v-bind:src="item.image" width="100%" />
      </div>
      <div class="modal_content">
        <h4>Dbut du voyage</h4>
        Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nunc et purus non odio pretium vestibulum. Sed eu varius est. Sed venenatis eros quam. Vestibulum quis ex ac nulla semper tristique non at risus. Sed placerat viverra nunc nec efficitur. Nulla vulputate neque a est sollicitudin rutrum pellentesque sed erat. Integer feugiat eu nibh nec lobortis. In semper quam vel tempor dictum. Maecenas vehicula rutrum justo non venenatis. Quisque lacinia a magna id fringilla. Mauris malesuada dolor eget nunc rutrum dignissim. Interdum et malesuada fames ac ante ipsum primis in faucibus.

        Praesent sollicitudin lacinia sapien, id blandit risus imperdiet sit amet. Integer a vulputate quam, at efficitur nisl. Cras tortor dui, dictum sollicitudin massa quis, dapibus pharetra massa. Donec massa sapien, finibus sit amet orci eget, vehicula cursus tellus. Nam ac felis purus. Vestibulum condimentum, mauris eget mattis auctor, eros nibh bibendum dolor, pulvinar aliquam metus libero id erat. Ut maximus nisl ac magna efficitur, at efficitur sem vestibulum. Etiam maximus scelerisque elit, a cursus sem placerat vel. Integer vel tempus metus. Maecenas vitae pretium orci, at convallis neque. Sed orci turpis, volutpat volutpat odio tincidunt, volutpat viverra erat. Nullam hendrerit massa blandit nibh tincidunt, eu rutrum lacus viverra. Curabitur faucibus molestie enim, et faucibus mi pharetra ut.
      </div>
    </sweet-modal>
  </div>
</template>

<script>
import { SweetModal } from 'sweet-modal-vue'

export default {
  components: {
    SweetModal
  },
  props: {
    open: Boolean,
    item: Object,
  },
  data() {
    return {
      openModal: this.open,
      itemSelected: ''
    }
  },
  watch: {
    open: function (valeur, ancienneValeur) {
      if(valeur) {
        this.itemSelected = this.item
        this.$refs.modal.open()
      }
    }
  },
  methods: {
    closeModal() {
      this.$emit('close')
    }
  }
};
</script>
<style>
</style>



