<template>
    <div>
        <headNav :login='login'></headNav>
        <admin v-if='login' :token='token'></admin>
        <masonry v-else></masonry>
    </div>
</template>
<script>
import url from 'url'
import auth from './../libs/auth'
import HeadNav from "../layout/appHeader.vue"
import Masonry from "./components/Masonry.vue"
import Admin from "./components/Admin.vue"


export default {
  name: "components",
  components: {
    HeadNav,
    Masonry,
    Admin
  },
  data() {
    return {
      login: false,
      token: ''
    }
  },
  mounted() {
    const json = url.parse(window.location.href, true);
    if(json.query.code && !this.login) {
      const _this = this;
      auth.getToken(json.query.code, (token) => {
        if(!token) {
          return console.log('ERROR TOKEN');
        }

        this.login = true;
        this.token = token.access_token;
      });
    }
  },

};
</script>



