<template>
    <section>
        <base-nav v-for="menu in menus"
                  :key="menu.type"
                  :type="menu.type"
                  effect="dark"
                  expand
                  title="Valou and Amandine"
                  :content-id="`navbar-${menu.type}`">
            <div class="row" slot="content-header" slot-scope="{closeMenu}">
                <div class="col-6 collapse-brand">
                    <a href="./index.html">
                        <img src="public/img/brand/blue.png">
                    </a>
                </div>
                <div class="col-6 collapse-close">
                    <close-button @click="closeMenu" :target="`navbar-${menu.type}`">

                    </close-button>
                </div>
            </div>
            <base-button
              tag="a"
              :href='linkAuth'
              type="white"
              block
              size="lg">
                {{ label }}
            </base-button>
            <!-- <component :is="menu.menuComponent"></component> -->
        </base-nav>
    </section>

</template>
<script>
import BaseNav from "../../components/BaseNav"
import CloseButton from "../../components/CloseButton"
import Menu1 from "./Navigation/Menu1"
import auth from '../../libs/auth'

export default {
  components: {
    BaseNav,
    CloseButton,
    Menu1,
  },
  data() {
    return {
      linkAuth: auth.auth(),
      label: 'Me connecter',
      menus: [
        { type: "success", menuComponent: Menu1 },
      ]
    };
  },
  props: {
    login: Boolean
  },
  created() {
    if (this.login) {
      this.label = "Se dconnecter"
      this.linkAuth = auth.logout()
    }
  }
};
</script>
<style>
</style>



