<template>
  <div v-else class="content-page">
    <div>
      <masonry :gutter="0" :cols="{default: 3, 1000: 3, 700: 2, 400: 1}">
        <div class="item" :data-index='index' v-for="(item, index) in albums" :key="index" @click='openModal(item)'>
          <div class='item_content'>
            <div class="item_content--img"><img v-bind:src="item.image" /></div>
            <span>{{item.title}}</span>
            <div class="item_content--date">12/08/18</div>
          </div>
        </div>
      </masonry>
    </div>
    <modal v-bind:open='open' v-bind:item='item' @close='closeModal'></modal>
  </div>
</template>
<script>
import firebase from '../../libs/firebase'
import Modal from './Modals/Infos.vue'

export default {
  components: {
    Modal
  },
  data() {
    return{
      login: false,
      load: true,
      albums: [],
      indexItem: null,
      open: false,
      item: {}
    }
  },
  created() {
    this.getEvents()

  },
  methods: {
    getEvents() {
      firebase.getData((events) => {
        this.albums = events
      });
    },
    openModal(item) {
      this.open = true
      this.item = item
    },
    closeModal() {
      this.open = false
    }
  }
};
</script>
<style>
</style>



